
.compDividerLine {
    margin-left: 2rem;
    margin-right: 2rem;
    margin-top: 5rem;
    margin-bottom: 5rem;
}

.body {
    margin-left: 5rem;
    margin-right: 5rem;
    margin-top: 3rem;
    margin-bottom: 3rem;
    text-align: center;
    font-size: 1.8rem;
}

.bodyText {
    margin-left: 5rem;
    margin-right: 5rem;
}

.imageStack {
    display: flex;
    gap: 2rem;
    justify-content: center; /* 수평 가운데 정렬 */
    align-items: center; /* 수직 가운데 정렬 */
    margin-top: 3rem;
    margin-bottom: 3rem;
}

.introImage {
    width: 20%;
    height: auto;
}

.introLink:link, .introLink:visited {
    text-decoration: none;
    color: #8D8D8D;
    font-size: 2rem;
}
.introLink:hover, .introLink:active {
    color: rgba(49, 49, 49);
    background-color: rgba(50, 69, 0, 0.03);
}

.midDot {
    font-size: 0.8rem;
    color: #D9D9D9;
    vertical-align: middle;
    padding: 8px;
}

.chevron_forward {
    width: 1.5rem;
    height: 1.5rem;
    margin-left: 0.5rem;
}

.chevron_down {
    width: 3rem;
    height: 1rem;
}