.sidebar {
    top: 0px;
    position: sticky;
    background-color: #939393;
    text-shadow: 3px 3px 5px rgba(0, 0, 0, 0.1);
    z-index: 10;
    width: 21rem;
    min-height: 55rem;
}

#topProfile {
    text-align: center;
}

#profileImage {
    width: auto;
    height: 100px;
    margin-top: 24%;
    margin-left: 3.6rem;
    z-index: 2;
    position: absolute;
}

#profileBackground {
    width: auto;
    height: 200px;
    margin-top: 10%;
    z-index: 1;
}

.textTag {
    margin-top: 3rem;
    margin-left: 1rem;
    font-size: 2rem;
    font-weight: 500;
}

.normalText {
    font-size: 1.3rem;
    font-weight: 300;
}

nav {
    font-family: 'Sunflower';
    padding-left: 27px;
    margin-top: 2rem;
    margin-bottom: 30px;
    font-size: 1.3rem;
}

.side-link, .side-link:visited {
    padding-left: 27px;
    text-decoration: none;
    color: white;
    display: block;
    font-size: 1.5em;
    transition: margin-left 0.3s;
  }
  
  .side-link:hover, .side-link:active {
    color: white;
    background-color: rgba(50, 69, 0, 0.2);
    margin-left: 30px;
  }  

  .side-link.active-link {
    color: rgb(255, 193, 78);
    font-weight: 800;
    text-shadow: 1px 1px 3px rgba(255, 166, 0, 0.2);
  }