.projectImage {
  width: 100%;
  height: auto;
  border-radius: 20px;
}

.projectTitleContainer {
  text-align: right;
  margin-right: 2rem;
}

.titleText {
  font-size: 4rem;
  font-weight: 700;
}

.periodText {
  font-size: 2rem;
}

.imageTitleContainer {
  margin-bottom: 1rem;
  text-align: left;
}

.imageTitleText {
  font-size: 2.5rem;
  font-weight: 500;
  margin-left: -10px;
}

.gifStack {
  display: flex;
  gap: 2rem;
  justify-content: center; /* 수평 가운데 정렬 */
  align-items: center; /* 수직 가운데 정렬 */
}