.login_input {
    font-family: 'SOYOMapleBoldTTF', sans-serif;
    font-size: 1rem;
    border: 1px solid #ccc;
    border-radius: 4px;
    padding: 8px;
    width: 8rem;
}

.login_flex {
    display: flex;
    margin-top: 0.7rem;
    padding-left: 1rem;
    gap: 3rem;
}

.login_container {
    display: flex;
    align-items: center;
    gap: 0.5rem;
}

.login_container.vertical {
    flex: 3;
    flex-direction: column;
    align-items: normal;
    padding-right: 2rem;
    width: 50%;
}

.login_button {
    color: #4c4c4c;
    font-size: 1.3rem;
    /* font-family: 'Sunflower'; */
    font-family: 'SOYOMapleBoldTTF', sans-serif;
    flex: 1;
    border: 1px solid #ccc;
    border-radius: 4px;
    padding: 8px;
    background-color: rgb(255, 193, 78);
    margin-top: 0.3rem;
    margin-right: 0.7rem;
    height: 6rem;
}

.login_button:hover {
    box-shadow: 0px 0px 10px rgba(255, 193, 78, 0.5);
}

#grayBtn {
    width: 2rem;
    height: 2rem;
    border: 1px solid #ccc;
    border-radius: 10px;
    padding: 8px;
    background-color: #939393;
    margin-top: 1rem;
    margin-right: 0.5rem;
    margin-left: 1rem;
}

#grayBtn:hover {
    box-shadow: 0px 0px 10px rgba(255, 193, 78, 0.5);
}

#navyBtn {
    width: 2rem;
    height: 2rem;
    border: 1px solid #ccc;
    border-radius: 10px;
    padding: 8px;
    background-color: DarkSlateGray;
    margin-top: 1rem;
    margin-right: 0.5rem;
}

#navyBtn:hover {
    box-shadow: 0px 0px 10px rgba(255, 193, 78, 0.5);
}

#blackBtn {
    width: 2rem;
    height: 2rem;
    border: 1px solid #ccc;
    border-radius: 10px;
    padding: 8px;
    background-color: lightslategray;
    margin-top: 1rem;
}

#blackBtn:hover {
    box-shadow: 0px 0px 10px rgba(255, 193, 78, 0.5);
}