@import url("https://fonts.googleapis.com/css2?family=Song+Myung&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Nanum+Pen+Script&display=swap");
@import url('https://fonts.googleapis.com/css2?family=Gowun+Dodum&family=Nanum+Gothic:wght@400;700;800&family=Sunflower:wght@300;500;700&display=swap');
@font-face {
  font-family: 'SOYOMapleBoldTTF';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2310@1.0/SOYOMapleBoldTTF.woff2') format('woff2');
  font-weight: 300;
  font-style: normal;
}

.container {
  display: flex;
  flex-direction: row;
  height: 1000px;
}

.sidebarContainer {
  position: relative;
  font-family: 'SOYOMapleBoldTTF', sans-serif;
  min-height: 55rem;
  transition: width 0.3s;
  box-shadow: 5px 0px 20px rgba(0, 0, 0, 0.2);
}

.sidebarContainer.visible {
  width: 26rem;
}

.sidebarContainer.hidden {
  width: 0rem;
}

.sidebarContainer_float {
  position: absolute;
  top: 0px;
  left: 0px;
  font-family: 'SOYOMapleBoldTTF', sans-serif;
  color: white;
  min-height: 55rem;
  height: 100%;
  transition: transform 0.3s;
  z-index: 2;
}

.sidebarContainer_float.visible {
  transform: translateX(0); 
}

.sidebarContainer_float.hidden {
  transform: translateX(-100%);
}

.sidebarButton {
  font-family: 'SOYOMapleBoldTTF', sans-serif;
  border-radius: 20px;
  border: none; 
  color: rgb(255, 193, 78);
  font-size: 3rem;
  font-weight: 700;
  text-align: center;
  width: 4rem;
  height: 4rem;
  z-index: 1;
  position: absolute;
  left: 20rem;
  box-shadow: 5px 0px 20px rgba(0, 0, 0, 0.2);
  transition: transform 0.3s;
}

.sidebarButton.fold {
  transform: translateX(0rem); 
}

.sidebarButton.open {
  transform: translateX(-21rem);
}

.sidebarButton_img {
  width: 1rem;
  height: 2.5rem;
  margin-bottom: 0.25rem;
}


.mainContainer{
  width: 100%;
  min-height: 55rem;
  font-family: 'SOYOMapleBoldTTF', sans-serif;
  background-color: whitesmoke;
  font-size: 30px;
  overflow: scroll;
  transition: transform 0.3s, width 0.3s;
}

.mainContainer.normal {
  width: 100%;
}

.mainContainer.extended {
  width: 100%;
}

.loginBox {
  font-family: 'SOYOMapleBoldTTF', sans-serif;
  font-size: 1.5rem;
  border: none; 
  background-color: #939393;
  box-shadow: 5px 0px 20px rgba(0, 0, 0, 0.2);
  color: white;
  width: 21rem;
  height: 8rem;
  z-index: 3;
  position: absolute;
  top: 0rem;
  right: 0rem;
  transition: transform 0.3s;
  
}

.loginBox.fold {
  transform: translateY(-21rem); 
  
}

.loginBox.open {
  transform: translateY(0rem);
}


.loginBoxButton {
  font-family: 'SOYOMapleBoldTTF', sans-serif;
  border-radius: 20px;
  border: none; 
  background-color: rgb(255, 193, 78);
  box-shadow: -5px 0px 20px rgba(0, 0, 0, 0.2);
  color: white;
  font-size: 2rem;
  width: 21rem;
  height: 16rem;
  z-index: 2;
  position: absolute;
  right: 0rem;
  top: -12rem;
  text-align: center; 
  transition: transform 0.3s;
}

.loginBoxButton.fold {
  transform: translateY(0rem); 
}

.loginBoxButton.open {
  transform: translateY(8rem);
}

.loginBoxButton_img {
  width: 3rem;
  height: 1rem;
  margin-top: 13rem;
  z-index: 3;
}

